<template>
	<view class="comment-container">
		<view class="ul">
			<span class="li change"><span v-for="(item,index) in change" :key='index' :class="{select:select == index}">{{item.text}}</span></span>
			<span class="li sort"><span v-for="(item,index) in sort" :key='index' :class="{select:selectSort == index}">{{item.text}}</span></span>	
		</view>
		<!-- <view class="comment" v-for='(item,index) in reply' :key='index'>
			<view class="user">
				<image :src="item.userimg" mode=""></image>
			</view>
			<view class="right">
				<view class="user-name">
				{{item.username}}
				</view>
				<view class="storey">
					第{{index+1}}楼<span class="time">{{time[index]}}</span>
				</view>
				<rich-text :nodes="item.txt" @click='replyComment(item)'></rich-text>
				<comment-son :storey='index+1' :fatherid='item.id'></comment-son>
			</view>
			<reply :articleid='articleid' :replyShow="replyShow" :replyType="2" :fatherid="fatherid">
				<span class='reply-comment'>
					回复<span class='name'>{{item.username}}</span>:
				</span>
			</reply>
		</view> -->
		<com  v-for="(item,index) in reply" :item='reply[index]' :articleid='articleid' :time='time' :key='index'>
			<!-- <reply slot='reply' :articleid='articleid' :replyShow="replyShow" :replyType="2" ></reply> -->
		</com>
	</view>
</template>

<script>
	import com from './com/com.vue'
	import commentSon from './commentSon/commentSon.vue'
	import reply from '../reply/reply.vue'
	export default {
		name:"comment",
		components:{commentSon,reply,com},
		data() {
			return {
				change:[{text:'全部回复'},{text:'只看楼主'}],
				sort:[{text:'热门'},{text:'正序'},{text:'倒序'}],
				select:0,
				selectSort:0,
				reply:[],
				time:[],
				replyShow:false,
				fatherid:1, //次级评论对应的父级评论的id
			};
		},
		props:['articleid'],
		methods:{
		},
		mounted() {
			this.$request('/querycomment',{
				articleid:this.articleid
			},'post').then(res =>{
				this.reply = res
				for(let i =0;i<res.length;i++){
					let second = Math.floor(((new Date().getTime() - this.reply[i].second)/1000))
					switch(true){
						case  second>86400:this.time[i] = this.reply[i].time;break;
						case  second>=3600:this.time[i] = Math.ceil(second/3600)+"小时前";break;
						case  second>=60:this.time[i] = Math.ceil(second/60)+'分钟前';break;
						case  second>0:this.time[i] = second+'秒前';break;						
					}
				}
				
			})
		},
	}
</script>

<style lang="scss">
	.comment-container{
		padding-bottom: 60px;
		.ul{
			height: 25px;
			font-size: 14px;
			margin: 20px 0;
			&:after{
				display: block;
				content: '';
				clear: both;
			}
			.li{
				float: left;
			}
			.change{
				span{
					display: inline-block;
					margin-right: 20px;
					height: 25px;
					line-height: 25px;
					text-align: center;
				}
				.select{
					font-weight: bold;
				}
			}
			.sort{
				float: right;
				padding: 2px;
				background-color: #E0E0E0;
				border-radius: 30px;
				span{
					display: inline-block;
					width: 40px;
					height: 25px;
					line-height: 25px;
					text-align: center;
					border-radius: 10px;
				}
				.select{
					background-color: #fff;
				}
			}
		}
		.comment{
			margin-bottom: 20px;
			.user{
				image{
					height: 40px;
					width: 40px;
					border-radius: 50%;
				}
			}
			.right{
				margin-left: 60px;
				margin-top: -40px;
				.user-name{
					height: 20px;
					line-height: 20px;
				}
				.storey{
					font-size: 14px;
					height: 20px;
					line-height: 20px;         
					color: #999;
					margin-bottom: 15px;
					.time{
						margin-left: 20rpx;
					}
				}
			}
			.reply-comment{
				display: inline-block;
				font-size: 12px;
				color: #999;
				margin: 10px 0;
				.name{
					margin: 0 3px;
				}
			}
		}
	}

</style>
